<template>
  <div>
    <el-menu
        class="el-menu-homepage"
        mode="horizontal"
        background-color="#1F4B62"
        text-color="#fff"
        active-text-color="#ffd04b"
        style="text-align: left">
      <el-tooltip class="item" effect="dark" content="点击回到主页" placement="bottom">
        <el-menu-item index="10" @click="backtoHomepage()" style="width: 180px;float: left">
          回到主页
        </el-menu-item>
      </el-tooltip>
    </el-menu>
    <el-container style="height: calc(100vh - 80px); border: 1px solid #eee">
      <el-aside width="200px" style="background-color: rgb(238, 241, 246)">
        <el-menu :default-openeds="['1', '3']">
          <el-submenu index="1">
            <template v-slot:title><i class="el-icon-message"></i>何</template>
            <el-menu-item-group>
              <router-link to="/personinfo"><el-menu-item index="1-1">用户信息</el-menu-item></router-link>
              <router-link to="/changeinfo"><el-menu-item index="1-2">修改信息</el-menu-item></router-link>
              <el-submenu index="10">
                <template v-slot:title>
                  我的订单
                </template>
                <el-menu-item index="20">
                  <router-link to="/orderwaitpay">
                    待付款订单
                  </router-link>
                </el-menu-item>
                <el-menu-item index="30">
                  <router-link to="/ordercheckedin">
                    已入住订单
                  </router-link>
                </el-menu-item>
                <el-menu-item index="40">
                  <router-link to="/orderwillcancel">
                    待取消订单
                  </router-link>
                </el-menu-item>
                <el-menu-item index="50">
                  <router-link to="/ordercanceled">
                    已取消订单
                  </router-link>
                </el-menu-item>
              </el-submenu>
              <router-view/>
            </el-menu-item-group>
          </el-submenu>
        </el-menu>
      </el-aside>

      <el-container>
        <el-header style="text-align: right; font-size: 12px">
          <router-link to="/personinfo">回到首页</router-link>
        </el-header>
        <el-main style="text-align: left;margin-left: 10%">
          <el-form ref="form" :model="info">
            <el-form-item label="ID">
              <span v-if="flag0=== false">{{info.u_id}}</span>
              <input v-if="flag0=== true" v-model="info.u_id">
              <el-button  icon="el-icon-edit" circle size="mini" v-on:click="change0"></el-button>
            </el-form-item>
            <el-form-item label="姓名">
              <span v-if="flag1=== false">{{info.uname}}</span>
              <input v-if="flag1=== true" v-model="info.uname">
              <el-button  icon="el-icon-edit" circle size="mini" v-on:click="change1"></el-button>
            </el-form-item>
            <el-form-item label="密码">
              <span v-if="flag2=== false">{{info.upassword}}</span>
              <input v-if="flag2=== true" v-model="info.upassword">
              <el-button  icon="el-icon-edit" circle size="mini" v-on:click="change2"></el-button>
            </el-form-item>
            <el-form-item label="邮箱">
              <span v-if="flag3===false" v-html="info.email"></span>
              <input v-if="flag3===true" v-model="info.email">
              <el-button  icon="el-icon-edit" circle size="mini" v-on:click="change3"></el-button>
            </el-form-item>
            <el-form-item label="性别">
              <span v-if="flag4===false" v-html="info.sex"></span>
              <el-radio-group v-if="flag4===true" v-model="info.sex">
                <el-radio label="男"></el-radio>
                <el-radio label="女"></el-radio>
              </el-radio-group>
              <el-button  icon="el-icon-edit" circle size="mini" v-on:click="change4"></el-button>
            </el-form-item>
            <el-form-item label="年龄">
              <span v-if="flag5===false" v-html="info.age"></span>
              <input v-if="flag5===true" v-model="info.age">
              <el-button class="el-icon-edit" circle size="mini" v-on:click="change5"></el-button>
            </el-form-item>
            <el-form-item label="个性签名">
              <span v-if="flag6===false" v-html="info.userdescribe"></span>
              <input v-if="flag6===true" v-model="info.userdescribe">
              <el-button  icon="el-icon-edit" circle size="mini" v-on:click="change6"></el-button>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="onSubmit">确认修改</el-button>
              <el-button v-on:click="cancel">取消</el-button>
            </el-form-item>
          </el-form>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: "changeinfo",
  data() {
    return {
      info: {
        u_id: '12',
        uname: '何',
        upassword: '123456',
        email: '1246@163.com',
        sex: '男',
        age: '20',
        userdescribe: '我是屌毛',
        shot: ''
      },
      session: [],
      flag0: false,
      flag1: false,
      flag2: false,
      flag3: false,
      flag4: false,
      flag5: false,
      flag6: false,
      flag7: false,
    }
  },
  methods: {
    backtoHomepage() {
      this.$router.push('/homepage')
    },
    change0() {
      // console.info('修改ID传回后端')
      if (this.flag0 === false) {
        this.flag0 = true
        console.info('true')
        return this.flag0
      }
      if (this.flag0 === true) {
        this.flag0 = false
        console.info('false')
        return this.flag0
      }
    },
    change1() {
      // console.info('修改姓名传回后端')
      if (this.flag1 === false) {
        this.flag1 = true
        console.info('true')
        return this.flag1
      }
      if (this.flag1 === true) {
        this.flag1 = false
        console.info('false')
        return this.flag1
      }
    },
    change2() {
      // console.info('修改密码传回后端')
      if (this.flag2 === false) {
        this.flag2 = true
        console.info('true')
        return this.flag2
      }
      if (this.flag2 === true) {
        this.flag2 = false
        console.info('false')
        return this.flag2
      }
    },
    change3() {
      // console.info('修改邮箱传回后端')
      if (this.flag3 === false) {
        this.flag3 = true
        console.info('true')
        return this.flag3
      }
      if (this.flag3 === true) {
        this.flag3 = false
        console.info('false')
        return this.flag3
      }
    },
    change4() {
      // console.info('修改性别传回后端')
      if (this.flag4 === false) {
        this.flag4 = true
        console.info('true')
        return this.flag4
      }
      if (this.flag4 === true) {
        this.flag4 = false
        console.info('false')
        return this.flag4
      }
    },
    change5() {
      // console.info('修改年龄传回后端')
      if (this.flag5 === false) {
        this.flag5 = true
        console.info('true')
        return this.flag5
      }
      if (this.flag5 === true) {
        this.flag5 = false
        console.info('false')
        return this.flag5
      }
    },
    change6() {
      // console.info('修改个性签名传回后端')
      if (this.flag6 === false) {
        this.flag6 = true
        console.info('true')
        return this.flag6
      }
      if (this.flag6 === true) {
        this.flag6 = false
        console.info('false')
        return this.flag6
      }
    },
    change7() {
      // console.info('修改头像传回后端')
      if (this.flag7 === false) {
        this.flag7 = true
        console.info('true')
        return this.flag7
      }
      if (this.flag7 === true) {
        this.flag7 = false
        console.info('false')
        return this.flag7
      }
    },
    openSuccess() {
      this.$message({
        showClose: true,
        message: '信息修改成功',
        type: 'success'
      })
    },
    openFail() {
      this.$message({
        showClose: true,
        message: '信息修改失败',
        type: 'error'
      })
    },
    onSubmit() {
      // console.log('修改个人数据')
      this.flag0 = false
      this.flag1 = false
      this.flag2 = false
      this.flag3 = false
      this.flag4 = false
      this.flag5 = false
      this.flag6 = false
      this.flag7 = false
      const self = this
      let postData = {
        u_id: self.session.u_id,
        uname: self.info.uname,
        age: self.info.age,
        sex: self.info.sex,
        userdescribe: self.info.userdescribe,
        email: self.info.email,
        shot: self.info.shot,
        upassword: self.info.upassword
      }
      console.log(JSON.stringify(postData))
      this.$axios({
        url: '/api/userinfo/UpdateUserinfo',
        method: 'POST',
        headers: {
          Authorization: 'Leco\'s test',
          'Content-Type': 'application/json'
        },
        data: JSON.stringify(postData)
      }).then(
          successResponse => {
            if (successResponse.data.message === '保存成功') {
              this.openSuccess()
            } else {
              this.openFail()
            }
          }).catch(err => {
        console.log(err, 'err')
      })
    },
  testSession () {
    const self = this
    this.$axios({
      url: '/api/userinfo/TestSession',
      method: 'POST'
    }).then(
        successResponse => {
          if (successResponse.data.data) {
            self.session = successResponse.data.data
            this.getUserInfo()
            // console.log(this.session)
            // eslint-disable-next-line no-empty
          } else {
          }
        }).catch(err => {
      console.log(err, 'err')
    })
  },
  getUserInfo () {
    const self = this
    this.$axios({
      url: '/api/userinfo/getById/' + self.session.u_id,
      method: 'GET'
    }).then(
        successResponse => {
          if (successResponse.data.data) {
            self.info = successResponse.data.data
            // console.log(self.info)
            // eslint-disable-next-line no-empty
          } else {
          }
        }).catch(err => {
      console.log(err, 'err')
    })
  }
},
created () {
  this.testSession()
}
}
</script>

<style scoped>
.el-input {
  width: 200px;
}
</style>
